:root {
    --body-color: white;
    --basic-font: 16px, sans-serif;
}

* {
    box-sizing:border-box;
}

html, body {
    width: 100%;
    min-height: 100vh;
    font-size: var(--basic-font);
    display: flex;
    flex-flow: column nowrap;
    margin: 0;
    padding: 0;
    background: var(--body-color);
}


ul, ol {
    margin: 0;
    padding: 0;
    list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
    padding: 0;
    margin: 0;
    font-weight: normal;
    font-style: normal;
}
a {
    text-decoration: none;
    display: block;
    color: #000;
}

.container {
    margin: 0 auto;
    padding: 0 10px;
}